<template>
    <div class="detail" id="notebook-list">
        <header>
            <a href="#" class="btn" @click.prevent="onCreate"> <i class="iconfont icon-plus"></i> </a>
        </header>
        <main>
            <div class="layout">
                <h3>({{notebooks.length}})</h3>
                <div class="book-list">
                    <router-link v-for="notebook in notebooks" :to="`/note?notebookId=${notebook.id}`" class="notebook"
                                 :key="notebook.id">
                        <div>
                            <span class="iconfont icon-notebook"><h3>{{notebook.title}}</h3></span>
                            <span>{{notebook.noteCounts}}</span>
                            <span class="action" @click.stop.prevent="onEdit(notebook)"></span>
                            <span class="action" @click.stop.prevent="onDelete(notebook)"></span>
                            <span class="date">{{notebook.createdAtFriendly}}</span>
                        </div>
                    </router-link>
                </div>
            </div>
        </main>
    </div>
</template>
<script>
    import  {friendlyDate} from '@/helpers/util'
    import {mapState, mapActions, mapGetters} from 'vuex'
    // window.NoteBooks = NoteBooks

    export  default {
        name: 'NoteBooksList',
        data(){
            return {}
        },
        created() {
            this.checkLogin({path: '/login'})

            this.getNotebooks()
        },
        computed: {
            ...mapGetters(['notebooks'])
        },
        methods: {
            ...mapActions(['addNotebook', 'updateNotebook', 'deleteNotebook', 'checkLogin', 'getNotebooks']),
            onCreate(){
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /^.{1,30}$/,
                    inputErrorMessage: '30'
                }).then(({value}) => {
                    this.addNotebook({title: value})
                })
            },
            onEdit(notebook){
                let title = ''
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /^.{1,35}$/,
                    inputValue: notebook.title,
                    inputErrorMessage: '30'
                }).then(({value}) => {
                    title = value
                    this.updateNotebook({notebookId: notebook.id, title})
                })
            },
            onDelete(notebook){
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteNotebook({notebookId: notebook.id})
                })
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    @import url(../assets/css/notebook-list.less);
</style>


